<template>
    <div id="allKq">
        <p id="rankp" style="font-size:18px;writing-mode: vertical-rl;">考勤排行榜</p>
        <div id="stream_rank" class="border_block">     
        </div>
        <div id="stream" class="border_block">
        </div>
        <p id="rankp" style="font-size:18px;writing-mode: vertical-rl;">考勤信息</p>
    </div>
</template>

<script>
import * as d3 from 'd3'
//import kqdata from './data/studentkq_test.json'
import Vue from 'vue'
import VueRouter from 'vue-router'
import Class from './Class.vue'
import axios from 'axios'

Vue.prototype.axios = axios


Vue.use(VueRouter)

var routes = [
  { path: '/Class', component: Class },
]

var router = new VueRouter({
  routes
})


export default {
    name: "allKq",
    props:['thisid'],
    mounted(){
        let thisvue = this
        axios.get('http://cuteanian.top/eduvis/mock/studentkq_test.json')
        .then(function (response) {
            thisvue.kqdata = response.data
            thisvue.draw()
        })
    },
    methods:{
        draw(){
            var allKq = this;

            var margin = {top: 5, right: 10, bottom: 20, left: 50},
                width = (window.innerWidth - margin.left - margin.right)*2,
                height = (window.innerWidth / 9. - margin.top - margin.bottom)*0.7;
            // append the svg object to the body of the page
            var svg = d3.select("#stream")
                .append("svg")
                // .attr("viewBox","0 0 "+(width + margin.left + margin.right)+" "+(height + margin.top + margin.bottom))
                // .attr("preserveAspectRatio","xMidYMid meet")
                .attr("width", width + margin.left + margin.right)
                .attr("height", height + margin.top + margin.bottom)
                .append("g")
                .attr("transform",
                    "translate(" + margin.left + "," + margin.top + ")");
            let streamLayer = svg.append("g"),
                streamBackLayer = svg.append("g");
            const kqControllerMap = {
                1001: {info: "迟到_晚到", index: 0},
                1002: {info: "校徽_早退", index: 1},
                1003: {info: "操场考勤机", index: 2},
                99001: {info: "迟到[移动考勤机]", index: 3},
                99002: {info: "校服[移动考勤机]", index: 4},
                99003: {info: "早退[移动考勤机]", index: 5},
                99004: {info: "离校[移动考勤机]", index: 6},
                99005: {info: "进校[移动考勤机]", index: 7}
            };
            const startDate = new Date("9/1/2016"),
                endDate = new Date("2/1/2019"),
                daySpan = parseInt((endDate - startDate) / (1000 * 60 * 60 * 24));
            const tickDates = ["9/1/2016", "1/1/2017", "6/1/2017", "9/1/2017", "1/1/2018", "6/1/2018", "9/1/2018", "1/1/2019"];
            const timeFormat = d3.timeFormat("%Y-%m-%d");
            const x = d3.scaleLinear().domain([0, daySpan]).range([ 0, width ]),
                reverseX = d3.scaleTime().domain([0, width]).range([startDate, endDate]),
                xLabel = d3.scaleTime().domain([startDate, endDate]).range([0, width]),
                y = d3.scaleLinear().domain([-3, 3]).range([ height, 0 ]);
            //const kqColor = d3.schemeDark2;
            //const kqColor = ["#ede58d",'#FF8C00',"#fdffe0","#ddf0ed","#FFEF94","#dafacc","#B9AAB9","#ffc0a1"]
            const d3Color = ['#8dd3c7','#ffffb3','#bebada','#fbd462','#80b1d3','#fb8072','#b3de69','#fccde5']
            // const d3Color = []
            // kqColor.forEach((d,i)=>{
            //     console.log(d3.color(d))
            //     d3Color.push(d3.color(d).darker(0.3).toString())
            //     d = d3.color(d).darker(0.3).toString()
            // })
            const dotRadius = 3,
                dotplotW = dotRadius * 2 * (daySpan + 1),
                dotplotH = dotRadius * 2 * 20;
            let dotplotSVG = d3.select("#dotplot")
                .append("svg").attr("id", "dotplotSVG")
                .attr("width", dotplotW + margin.left + margin.right)
                .attr("height", dotplotH + margin.top + margin.bottom)
                .append("g")
                .attr("transform", "translate(" + margin.left + "," + margin.top + ")");
            let dotBackLayer = dotplotSVG.append("g");
            const dotX = d3.scaleLinear().domain([0, daySpan]).range([ 0, dotplotW ]),
                dotReverseX = d3.scaleTime().domain([0, dotplotW]).range([startDate, endDate]),
                dotXLabel = d3.scaleTime().domain([startDate, endDate]).range([0, dotplotW]);
            // check if user select a dot
            let selectId = "";
            let tooltip = d3.select("body").append("div")
                .attr("class", "tooltip")
                .style("opacity", 0);
            function getEmptyItem(day){
                let item = {day: day};
                d3.keys(kqControllerMap).forEach(key=>{
                    item[key] = 0;
                });
                return item;
            }
            function initData(){
                let data = [];
                for(let i = 0; i < daySpan; i++){
                    data.push(getEmptyItem(i));
                }
                return data;
            }
            function drawStreamGraph(data, result){
                drawClassRank(result);
                // draw x axis
                let xaxis = svg.append('g')
                    .attr("transform", "translate(0," + height/2 + ")")
                    .call(d3.axisBottom(xLabel)
                        .tickFormat(timeFormat)
                        .tickValues(tickDates.map(function(d) { return new Date(d)}) ));
                xaxis.selectAll("text").style("font-size","14px").attr("transform", "translate(40," + 0 + ")")
                
                //let values = ['10^3','10^2','10^1','0','10^-1','10^-2','10^-3']
                // draw y axis
                let yAxis = d3.axisLeft(y).ticks(6)
                //.tickFormat(d3.format())
                let yaxis = svg.append("g")
                    .call(yAxis)

                yaxis.selectAll("text").text(function(d,i){
                    if(d==0)
                        return 0
                    else
                        return "10^"+Math.abs(d)
                })

                document.getElementById("stream").addEventListener('scroll', function(evt) { 
                    // console.log(svg.node())
                    // svg.node().setAttribute('y', 10 + this.scrollTop); 

                    //d3.selectAll("tspan").attr("y",this.scrollTop-5)
                    console.log(this.scrollLeft)
                    // yaxis.selectAll("path").attr("transform", "translate("+this.scrollLeft + "0)")
                    yaxis.selectAll("line").attr("x",this.scrollLeft-10)
                    yaxis.selectAll("text").attr("x",this.scrollLeft-10)
                    //yaxis.attr("transform", "translate("+this.scrollLeft + "0)")
                    //yaxis.attr("x",this.scrollLeft)
                }, false)


                
                let stackedData = d3.stack()
                    .offset(d3.stackOffsetSilhouette)
                    .keys(d3.keys(kqControllerMap))
                    (data);
                const line = d3.line().x(d=>d[0]).y(d=>d[1]);
                let highlight = streamBackLayer.append("path")
                    .attr("d", line([[0, 0], [0, height]]))
                    .attr("stroke", "black")
                    .attr("stroke-width", 1);
                let dateText = streamBackLayer.append("text")
                    .attr("font-size", "12px")
                    .attr("text-anchor", "left")
                    .attr("x", "0.5em")
                    .attr("y", 0)
                    .attr("dx", 0)
                    .attr("dy", "0.5em");
                const mousemove = ()=>{
                        let mouse = d3.mouse(d3.select('#streamBack').node());
                        highlight.attr("opacity", 1)
                            .attr("d", line([[mouse[0], 0], [mouse[0], height]]));
                        dateText.attr("opacity", 1).attr("x", mouse[0]).text(timeFormat(reverseX(mouse[0])));
                    },
                    mouseout = ()=>{
                        highlight.transition().duration(500).attr("opacity", 0);
                        dateText.transition().duration(500).attr("opacity", 0);
                    };
                   
                streamBackLayer
                    .append("rect")
                    .attr("id", "streamBack")
                    .attr("x", 0).attr("y", 0)
                    .attr("width", width).attr("height", height + margin.top + margin.bottom)
                    .attr("fill", "white").attr("opacity", 0)
                    .on("mousemove", mousemove)
                    .on("mouseout", mouseout)
                    
                // Show the areas
                streamLayer.selectAll("mylayers")
                    .data(stackedData)
                    .enter()
                    .append("path")
                    .style("fill", d=>d3Color[kqControllerMap[d.key].index])
                    .attr("d", d3.area()
                        .curve(d3.curveMonotoneX)
                        .x(function(d) {
                            // var date = new Date();
                            // date.setDate(startDate.getDate()+d.data.day);
                            // return x(date);
                            return x(d.data.day);
                        })
                        .y0(function(d) {
                            return d[0] == 0 ? y(0) : y( Math.sign(d[0]) * Math.log10(Math.abs(d[0]) + 1));
                        })
                        .y1(function(d) {
                            // return y(d[1]);
                            return d[1] == 0 ? y(0) :  y(Math.sign(d[1]) * Math.log10(Math.abs(d[1]) + 1));
                        })
                    );
                streamBackLayer
                    .call( d3.brushX()                     // Add the brush feature using the d3.brush function
                        .extent( [ [0,0], [width, height] ] )
                        .on("end", updateChart)
                    );
                function updateChart() {
                    // Get the selection coordinate
                    let extent = d3.event.selection;
                    d3.select("#stream_rank").select("svg").remove();
                    if(extent){
                        let dayExtend = [parseInt((reverseX(extent[0]) - startDate) / (1000 * 60 * 60 * 24)),
                            parseInt((reverseX(extent[1]) - startDate) / (1000 * 60 * 60 * 24))];
                        drawClassRank(result.filter(d=> d.day >= dayExtend[0] && d.day <= dayExtend[1]));
                    }
                    else{
                        drawClassRank(result);
                    }
                }
            }
                function getDotPlotData(data) {
                    let result = [];
                    data.forEach(student=>{
                        student.kqdata.forEach(d=>{
                            d.stud_id = student.bf_studentID;
                            d.stud_name = student.bf_Name;
                            d.cla_id = student.bf_classid;
                            d.cla_name = student.cla_Name;
                            result.push(d);
                        });
                    });
                    return result;
                }
            function drawClassRank(data){
                let result = d3.nest()
                    .key(d=>d.cla_id)
                    .rollup(function(leaves) { return leaves; })
                    .entries(data)
                    .sort((a,b)=>b.value.length-a.value.length);
                const widthBar = 20, marginBar = 2, heightToplist = (widthBar + marginBar) * result.length;
                let toplistSVG = d3.select("#stream_rank")
                    .append("svg").attr("width", 100).attr("height", heightToplist).append("g");
                let back = toplistSVG.append("rect").attr("x", 0).attr("y", 0)
                    .attr("width", 100).attr("height", heightToplist).attr("fill", "white");
                let map = d3.map();
                for(let i = 0; i < result.length; i++){
                    let item = {
                        count: result[i].value.length,
                        idx: i,
                        cla_id: result[i].value[0].cla_id,
                        cla_name: result[i].value[0].cla_name
                    };
                    d3.keys(kqControllerMap).forEach(key=>{
                        item[key] = 0;
                    });
                    map.set(result[i].key, item);
                }
                data.forEach(d=>{
                    map.get(d.cla_id)[d.ControllerID] += 1;
                });
                const topsData = map.values().sort((a,b) => b.count - a.count);
                let stack = d3.stack()
                    .keys(d3.keys(kqControllerMap))
                    .order(d3.stackOrderNone)
                    .offset(d3.stackOffsetNone);
                let series = stack(topsData);
                // set the scale of x and y
                let ydomain = [], yrange = [];
                map.keys().forEach((key)=>{
                    ydomain.push(key);
                    yrange.push(map.get(key).idx * (widthBar + marginBar));
                });
                const x = d3.scaleLinear().domain([0, d3.max(topsData, d=>d.count)]).range([0, 100]);
                const y = d3.scaleOrdinal().domain(ydomain).range(yrange);
                let groups = toplistSVG
                    .selectAll(".bargroup")
                    .data(series)
                    .enter().append("g")
                    .attr("class", "bargroup")
                    .attr("fill", d=>d3Color[kqControllerMap[d.key].index]);
                const mouseover = d=>{
                        tooltip.transition()
                            .duration(200)
                            .style("opacity", .9);
                        let html = `班级: ${d.data.cla_name}<br/>总数: ${d.data.count}`;
                        tooltip.html(html)
                            .style("left", (d3.event.pageX) + "px")
                            .style("top", (d3.event.pageY - 28) + "px");
                    },
                    mouseout = ()=>{
                        tooltip.transition().duration(500).style("opacity", 0);
                    },
                    unclick = ()=>{
                        selectId = "";
                        dotplotSVG.selectAll(".dot").attr("opacity", 0.6);
                        toplistSVG.selectAll(".bar").attr("opacity", 1);
                    },
                    click = (d)=>{
                        allKq.$emit('clickClass', d.data.cla_id)
                    },
                     dblclick = d=>{
                        console.log(d)
                        router.push({
                            path: '/class',
                            query: {
                                id: d.data.cla_id
                            }
                        })
                        router.go()
                    };
                groups.selectAll("rect")
                    .data(d => d).enter()
                    .append("rect")
                    .attr("class", d=> `bar bar-${d.data.cla_id}`)
                    .attr("x", d=> x(d[0]))
                    .attr("y", d=> y(d.data.cla_id))
                    .attr("height", widthBar)
                    .attr("width", d=>x(d[1]) - x(d[0]))
                    .on("mouseover", mouseover)
                    .on("mouseout", mouseout)
                    .on("click",click)
                    .on("dblclick",dblclick)
                back.on("click", unclick);
            }

            //画出stream

            let data = initData();
                this.kqdata.forEach(student=>{
                    student.kqdata.forEach(d=>{
                        let time = d.DataDateTime.split(' ')[0].split('/');
                        let date = new Date(time[0], time[1] - 1, time[2]);
                        let diffDays = parseInt((date - startDate) / (1000 * 60 * 60 * 24));
                        d.day = diffDays;
                        if(diffDays > 0) data[diffDays][d.ControllerID] += 1;
                    });
            });
            const result = getDotPlotData(this.kqdata);
             drawStreamGraph(data, result, 10);

        }
    }
}

</Script>

<style scopedSlots>

     #allKq{
         display:flex;
         justify-content: center;
     }

     #kq{
         display: block;
         height:20px;
     }


     div #stream{
            border:2px solid #000;
            overflow-y: auto;
            position: relative;
            width: 80%;
            height: calc(100vh * 0.16)
        }
    div #stream_rank{
            height: calc(100vh * 0.16);
            position: relative;
            width: 120px;
            overflow-y: auto;
        }


</style>
