/* eslint-disable no-console */
import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import Home from './components/Home.vue'
import Class from './components/Class.vue'
import Choose from './components/Choose.vue'
import Student from './components/Student.vue'
import Cos from './components/cos.vue'
import menu from './components/menu.vue'


//import NotFound from './components/NotFound.vue'
import ElementUI from "element-ui"
Vue.use(ElementUI)//全局使用ElementUI
import 'element-ui/lib/theme-chalk/index.css'



Vue.use(VueRouter)

Vue.directive('move', {
  inserted: function(a) {
    // 鼠标按下事件
    a.onmousedown = function(e) {
      var disX = e.clientX - a.offsetLeft
      var disY = e.clientY - a.offsetTop

      if (a.setCapture) {
        a.setCapture()
      }
      // 鼠标移动事件-----给文档流绑定移动事件
      document.onmousemove = function(e) {
        e.preventDefault()
        var L = e.clientX - disX
        var T = e.clientY - disY

        L = Math.min(Math.max(L, 0), document.documentElement.clientWidth - a.offsetWidth)
        T = Math.min(Math.max(T, 0), document.documentElement.clientHeight - a.offsetHeight)

        a.style.left = L + 'px'
        a.style.top = T + 'px'
      }
      // 鼠标离开事件
      document.onmouseup = function() {
        document.onmousemove = document.onmousedown = null
        if (a.releaseCapture) {
          a.releaseCapture()// 拖动后在解除事件锁定
        }
      }
    }
  }
})


var routes = [
  { path: '/Home', component: Home },
  { path: '/Choose', component: Choose },
  { path: '/Class', component: Class },
  { path:'/student',component:Student},
  { path:'/cos',component:Cos},
  { path:'*',component: menu},
  { path:'menu',component:menu}
]

var router = new VueRouter({
  routes
})

new Vue({
  router: router,
  render: h => h(App)
}).$mount('#app')

