<template>
<div id="studentinfo">
    <el-row type="flex" class="row-bg" justify="center">
    <el-col :span="10" style="box-shadow: rgba(0, 0, 0, 0.12) 0px 2px 4px;" >   
    <div class="ele" id="summary">
            <div id="info">
                        <div v-if="studentSex == '女'" class='infoitem'>
                            <img src="./image/girl.png" class="seximg">
                        </div>
                        <div v-else-if="studentSex == '男'" class='infoitem'>
                            <img src="./image/boy.png" class="seximg">
                        </div>
                        <div class='infoitem'>
                            <table id="box-table-b" summary="Most Favorit Movies">
                                <tbody>
                                    <tr>
                                        <th scope="row">学生ID</th>
                                        <td>{{studentId}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">学生姓名</th>
                                        <td>{{studentName}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">性别</th>
                                        <td>{{studentSex}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">学生班级</th>
                                        <td>{{studentCla}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">政治面貌</th>
                                        <td>{{polic}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">学生住址</th>
                                        <td>{{studentAd}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">宿舍</th>
                                        <td>{{studentroom}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">是否退学</th>
                                        <td>{{leaveSchool}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">成绩排名</th>
                                        <td>{{grademean}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">成绩浮动</th>
                                        <td>{{gradevar}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">消费</th>
                                        <td>{{cos}}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">考勤</th>
                                        <td>{{kq}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
            </div>  
            <studentGradeSum :studentId=this.studentId :choose = this.choose></studentGradeSum>
        </div>
    </el-col>
    <el-col :span="18">   
    <div class="detail">
        <studentGrade :studentId = this.studentId @trans="transChoose"></studentGrade>
        <studentCos :studentId = this.studentId></studentCos>
        <studentKq :studentId = this.studentId></studentKq>
    </div>
    </el-col>
    </el-row>
</div>
</template>


<script>
import Vue from 'vue'

import studentGrade from "./studentGrade.vue"
import studentGradeSum from "./studentGradeSum.vue"
//import studentInfo from "./data/studentInfo.json"
import studentCos from "./studentCos.vue"
import studentKq from "./studentKq.vue"
import axios from 'axios'
import * as d3 from 'd3'
import Driver from 'driver.js';
import 'driver.js/dist/driver.min.css';

Vue.prototype.axios = axios




export default {
    name:"student",
    components:{
        studentGrade,
        studentGradeSum,
        studentCos,
        studentKq,
    },
    mounted(){
        d3.selectAll(".toprightchoose").remove()
        d3.selectAll(".toprightcos").remove()
        d3.selectAll(".classname").remove()
        let thisvue = this;
        axios.get('http://cuteanian.top/eduvis/mock/studentInfo.json')
        .then(function (response) {
            thisvue.studentInfo = response.data;
            thisvue.studentId = thisvue.$route.query.studentId
            let thisStudent = thisvue.studentInfo[thisvue.studentId]
            thisvue.studentName = thisStudent['bf_Name']
            thisvue.studentCla = thisStudent['cla_Name']
            thisvue.studentAd = thisStudent['bf_NativePlace']
            thisvue.studentSex = thisStudent['bf_sex']
            thisvue.studentroom = thisStudent['bf_qinshihao']
            thisvue.leaveSchool = thisStudent['bf_leaveSchool']
            thisvue.polic = thisStudent['bf_policy']
            if(thisvue.leaveSchool==''){
                thisvue.leaveSchool="否"
            }
            else{
                thisvue.leaveSchool="是"
            }
            if(thisvue.studentroom=='')
                thisvue.studentroom = "非住校生"
            }) 
            thisvue.driver()
    },
    created(){
        let thisvue = this
        axios.get('http://cuteanian.top/eduvis/mock/studentSummary.json')
        .then(function(response){
            thisvue.studentSummary = response.data[thisvue.studentId]
            console.log(thisvue.studentSummary)
        })   
    },
    data(){
        return {
            studentId:this.$route.query.studentId,
            studentName:"",
            studentCla:"",
            stuidentAd:"",
            studentSex:"",
            studentroom:"",
            leaveSchool:"",
            polic:"",
            choose:'',
            studentInfo:{},
            studentSummary:{}
        }
    },
    computed:{
        grademean:function(){
            if(this.studentSummary==undefined){
                return "无信息"
            }
            let rankmean = this.studentSummary.meanRank;
            console.log(rankmean)
            let result;
            if(rankmean<0){
                    result = "无成绩信息"
            }
            else if(rankmean<0.15){
                result = "前15%,等级A"

            }
            else if(rankmean<0.45){
                result = "前45%,等级B"

            }
            else if(rankmean<0.95){
                result = "前95%,等级D"
            }
            else if(rankmean<1){
                result = "前100%,等级E"
            }
            return result;    
        },
        gradevar:function(){
            if(this.studentSummary==undefined){
                return "无信息"
            }
            let rankvar = this.studentSummary.varRank;
            let finalresult
            function toNonExponential(num) {
                var m = num.toExponential().match(/\d(?:\.(\d*))?e([+-]\d+)/);
                return num.toFixed(Math.max(0, (m[1] || '').length - m[2]));
            }
            let rankvarN = toNonExponential(rankvar)
            console.log(rankvarN)
            if(rankvar<0){
                finalresult="无成绩信息"
            }
            else if(rankvarN<0.00014771933447626275){
                finalresult="较稳定"
            }
            else{
                finalresult= "浮动较大"
            }
            return finalresult;
        },
        kq:function(){
            if(this.studentSummary==undefined){
                return "无信息"
            }
            let kq = this.studentSummary.kq;
            let result;
            if(kq==0){
                result = "优秀"
            }
            else if(kq<5){
                result = "良好"
            }
            else if(kq<10){
                result = "较差"
            }else{
                result = "糟糕"
            }
            return result;

        },
        cos:function(){
            let cos = this.studentSummary.cos;
            let result = ""
            if(cos<13){
                result = "节俭"
            }
            else if(cos<33){
                result = "普通"
            }else{
                result = "较高"
            }
            return result
        }

    },
    methods:{
        driver(){
            const driver = new Driver();
            // Define the steps for introduction
            driver.defineSteps([
            {
                element: '#chart',
                popover: {
                className: 'first-step-popover-class',
                title: 'Title on Popover',
                description: 'Body of the popover',
                position: 'top'
                }
            }
            ]);
            // Start the introduction
            driver.start();

      },
        transChoose(choose){
            console.log(choose)
            this.choose = choose

        }
    }
}
</script>


