  methods: {
    open() {
        this.$prompt('请输入需要查询的班级id', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^\d{3}$/,
          inputErrorMessage: 'ID格式不正确',
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: '班级ID: ' + value
          });
          router.push({
                path: '/class',
                query: {
                    id: value
                }
            }),
            router.go()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });       
        });
      },
      searchStudent(){
