<script>


import * as d3 from 'd3'
//import kqdata from './data/studentkq_test.json'
import Vue from 'vue'
import VueRouter from 'vue-router'
import Driver from 'driver.js';
import 'driver.js/dist/driver.min.css';
import annotaion from 'd3-svg-annotation'


export default {
    name:"menu",
    mounted(){
        let screenWidth = document.body.clientWidth
            var margin = {top: 40, right: 40, bottom: 0, left: 0},
                width = window.innerWidth*0.18,
                height = window.innerWidth*0.18;
        let thissvg = d3.select(".excel").append("svg")
                .attr("width", width + margin.left + margin.right)
                .attr("height", height + margin.top + margin.bottom)
        let svg = thissvg.append("g")
                .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

        let textsvg = thissvg.append("g")
                .attr("transform", "translate(" + margin.left + "," + margin.top + ")");
        let arrowsvg = thissvg.append("g")
                .attr("transform", "translate(" + margin.left + "," + margin.top + ")");
        
        let rectwidth = (width-margin.right)/3-20
        let rectheight = (height-margin.top)/3-20
        this.rectwidth = rectwidth;
        this.rectheight = rectheight

        let data=[0,1,2,3,4]
        let name = ['A','B','C','D','E']




        const mouseover = d=>{
            document.getElementById("aa"+d.toString()).style.backgroundColor="#d3dce5"
                    
                },
        mouseout = d=>{
                for(let i=0;i<5;i++){
                    document.getElementById("aa"+i.toString()).style.backgroundColor="#e9eef3"
                }
            }



        svg.selectAll("rect")
        .data(data)
        .enter()
        .append("rect")
        .attr("height",function(d,i){
            let normal = [0,1,4]
            if(normal.indexOf(i)>=0){
                return rectheight
            }
            else{
                return (rectheight+10)*2
            }
        })
        .attr("width",function(d,i){
            if(i<4){
                return rectwidth
            }else{
                return (rectwidth+13)*3
            }
        })
        .attr("fill","#f5f7fa")
        .attr("rx",10)
        .attr("ry",10)
        .attr("x",function(d,i){
            let first = [0,1,4]
            if(first.indexOf(i)>=0){
                return margin.right
            }
            else{
                return (i-1)*(rectwidth+20)+margin.right
            }

        })
        .attr("y",function(d,i){
            let first = [0,2,3]
            if(first.indexOf(i)>=0){
                return 0
            }
            else if(i==1){
                return rectheight+20
            }
            else if(i==4){
                return (rectheight+20)*2
            }
        })
        .on("mouseover",mouseover)
        .on("mouseout",mouseout)
        
        let label = ["成绩/考勤","选课","消费","年级","班级","个人"]

        textsvg.selectAll("text")
        .data(name)
        .enter()
        .append("text")
        .text(d => d)
        .attr("id",d=>d)
        .attr("x",function(d,i){
            let row=[1,2]
            if(row.indexOf(i)>=0){
                return i*(rectwidth+20)+rectwidth/3+margin.right
            }
            else if(i==3){
                return margin.right+rectwidth/2
            }
            else if(i==4){
                return margin.right+rectwidth*1.5
            }
            else if(i==0){
                return margin.right+rectwidth/2.5
            }
        })
        .attr("y",function(d,i){
            let column = [3,4]
            if(i==3){
                return margin.top+rectheight*1.3
            }
            else if(i==4){
                return rectheight*2+margin.top+rectheight/2
            }
            else if(i==0){
                return rectheight/2;
            }
            else{
                return rectheight;
            }
        })
        .style("font-size","20px")
        .attr("fill","grey")

        

        svg.selectAll("text")
        .data(label)
        .enter()
        .append("text")
        .text(d => d)
        .attr("x",function(d,i){
            let row=[1,2]
            if(row.indexOf(i)>=0){
                return i*(rectwidth+20)+rectwidth/3+margin.right
            }
            else if(i==0){
                return margin.right
            }
        })
        .attr("y",function(d,i){
            let column = [3,4,5]
            if(i==3){
                return margin.top
            }
            else if(i==4){
                return rectheight+margin.top+rectheight/3
            }
            else if(i==5){
                return rectheight*2+margin.top+rectheight/2
            }
            else{
                return -10;
            }
        })
        .style("font-size","14px")
        .attr("fill","grey")
        this.drawArrow()
        let button = document.getElementById("helpbutton")

        button.addEventListener('click', ({target}) => {
           event.stopPropagation()
           this.help()
       })

    },
    methods:{
        help(){
            const driver = new Driver({
            });
            // Define the steps for introduction
            driver.defineSteps([
            {
                element: '#a1',
                popover: {
                title: '系统介绍',
                position: 'bottom'
                }
            },
            {
                element: '#a2',
                popover: {
                title: '进入成绩/考勤页面',
                position: 'bottom'
                }
            },
            {
                element: '#a3',
                popover: {
                title: '进入选课页面',
                position: 'bottom'
                }
            },
            {
                element: '#a4',
                popover: {
                title: '进入消费页面',
                position: 'bottom'
                }
            },
            {
                element: '.input',
                popover: {
                title: '输入学生id进入学生主页',
                position: 'bottom'
                }
            }
            ]);
            driver.start();
      },
      drawArrow(){
        const type = annotaion.annotationLabel
        const annotations = [{
          note: {
            // label: "Changed connector type to 'curve'",
            // title: "d3.annotationLabel",
            // wrap: 150
          },
          connector: {
            end: "arrow",
            //type: "curve",
            //can also add a curve type, e.g. curve: d3.curveStep
            //points: [[100, 14],[190, 52]]
          },
            x: d3.select("#D").attr("x"), 
            y: d3.select("#D").attr("y"),
            ny: d3.select("#A").attr("y"), 
            nx: d3.select("#A").attr("x"),
          
        },{
          note: {
            // label: "Changed connector type to 'curve'",
            // title: "d3.annotationLabel",
            // wrap: 150
          },
          connector: {
            end: "arrow",
            //type: "curve",
            //can also add a curve type, e.g. curve: d3.curveStep
            //points: [[100, 14],[190, 52]]
          },
            x: d3.select("#D").attr("x"), 
            y: d3.select("#D").attr("y"),
            ny: d3.select("#B").attr("y"), 
            nx: d3.select("#B").attr("x"),
          
        },{
          note: {
            // label: "Changed connector type to 'curve'",
            // title: "d3.annotationLabel",
            // wrap: 150
          },
          connector: {
            end: "arrow",
            //type: "curve",
            //can also add a curve type, e.g. curve: d3.curveStep
            //points: [[100, 14],[190, 52]]
          },
            x: d3.select("#E").attr("x"), 
            y: d3.select("#E").attr("y"),
            ny: d3.select("#C").attr("y"), 
            nx: d3.select("#C").attr("x"),
          
        },{
          note: {
            // label: "Changed connector type to 'curve'",
            // title: "d3.annotationLabel",
            // wrap: 150
          },
          connector: {
            end: "arrow",
            //type: "curve",
            //can also add a curve type, e.g. curve: d3.curveStep
            //points: [[100, 14],[190, 52]]
          },
            x: d3.select("#E").attr("x"), 
            y: d3.select("#E").attr("y"),
            ny: d3.select("#D").attr("y"), 
            nx: d3.select("#D").attr("x"),
          
        }
        ].map(function(d){ d.color = "#8da0cb"; return d})

        const makeAnnotations = annotaion.annotation()
          .type(annotaion.annotationLabel)
          .annotations(annotations)

        d3.select("svg")
          .append("g")
          .attr("class", "annotation-group")
          .call(makeAnnotations)
          .attr("transform", "translate(" + 0 + "," + 30 + ")");
    }
    }
}
</script>

