    computed:{
        xScale:function(){
            var margin = {top: 50, right:0, bottom: 100, left: 100};
            margin.right = this.margin.right;
            margin.left = this.margin.left;
            var width = this.width - margin.left - margin.right,
            height = this.height - margin.top - margin.bottom;

            var x = d3.scaleBand()
            .range([ 0, width])
            .padding(0.01);

            return x;

        },
        yScale:function(){
            var yScale = d3.scaleBand()
                        .range([ height, 0 ])
                        .domain(this.subs)
                        .padding(0.01);
            let margin = {top: 0, right: 0, bottom: 100, left: 0}
            margin.bottom = this.margin.bottom;
            margin.top = this.margin.top;
            let width = this.width*0.15 - margin.left - margin.right
            let height = this.height - margin.top - margin.bottom
            yScale.range([ height-20, 0 ])
            return yScale;
        }
    },
