        drawXaxis(svg,prob){
            let margin = {top: 0, right: 20, bottom: 100, left: 0}
            let width = this.width*prob - margin.left - margin.right;
            let height = this.height - margin.top - margin.bottom
            let xScale = d3.scaleLinear().rangeRound([0, width])
            xScale.domain([0, this.maxx])
            let xAxis = d3.axisBottom(xScale)
            svg.append("g")
                .attr("class", "axis axis--x")
                .attr("transform", "translate(0," + (height+5) + ")")
                .call(xAxis);
        },
        drawYaxis(stackData,svg,prob,ymax){
            var margin = {top: 10, right: 20, bottom: 0, left: 100};
            margin.right = this.margin.right;
            margin.left = this.margin.left;

            var width = this.width - margin.left - margin.right,
                height = this.height*prob - margin.top - margin.bottom;

            let y = d3.scaleLinear()
                    .domain([0,ymax])
                    //.domain([0, d3.max(stackData, d => d3.max(d, d => d[1]))])
                    .rangeRound([height - margin.bottom, margin.top])

            let yAxis = g => g
                .attr("transform", "translate(" + margin.left + "," + 0 + ")")
                .call(d3.axisLeft(y).ticks(3))
                //.ticks(null, "s"))
                .call(g => g.selectAll(".domain").remove())
            svg.append("g")
                .call(yAxis);


        },
