<<template>
    <div class="ele" style="box-shadow : 0 2px 4px rgba(0, 0, 0, .12)">
        <h5 style="margin-right:40%">考勤记录</h5>
        <div id="skq">
            <div id="kqvis"></div>
            <div id="legend"></div>
        </div>
    </div>
</template>

<script>
import * as d3 from 'd3'
import echarts from 'echarts'
import Vue from 'vue'
import axios from 'axios'

Vue.prototype.axios = axios
//import studentkq from "./data/studentkqd.json"

export default {
    name:'studentKq',
    props:['studentId'],
    mounted(){

        let thisvue = this;

        axios.get('http://cuteanian.top/eduvis/mock/studentkqd.json')
        .then(function (response) {
            thisvue.studentkq = response.data;
            thisvue.getdata()
        })
        
    },
    methods:{
        getdata(){
                let data = this.studentkq[this.studentId]
                this.draw(data)

        },
        draw(data){
            const dotRadius = 6,
                textH = 20,
                width = dotRadius * 2 * 36,
                height = dotRadius * 2 * 15 + textH;
            const startY = 2016, startM = 9, endY = 2019, endM = 6;
            const kqControllerMap = {
                1001: {info: "迟到_晚到", index: 0},
                1002: {info: "校徽_早退", index: 1},
                1003: {info: "操场考勤机", index: 2},
                99001: {info: "迟到[移动考勤机]", index: 3},
                99002: {info: "校服[移动考勤机]", index: 4},
                99003: {info: "早退[移动考勤机]", index: 5},
                99004: {info: "离校[移动考勤机]", index: 6},
                99005: {info: "进校[移动考勤机]", index: 7}
            };
            //const kqColor = ["#ede58d",'#FF8C00',"#fdffe0","#ddf0ed","#FFEF94","#dafacc","#B9AAB9","#ffc0a1"]
            const kqColor = ['#8dd3c7','#ffffb3','#bebada','#fbd462','#80b1d3','#fb8072','#b3de69','#fccde5']

            const kqControlTaskMap = {
                    100000: "默认信息",
                    100100: "早上迟到",
                    100200: "晚到学校",
                    100300: "晚自修迟到",
                    200000: "默认信息",
                    200100: "校徽校服",
                    200200: "请假离校",
                    300000: "默认信息",
                    300100: "住宿早晨锻炼",
                    300200: "课间操请假",
                    9900100: "默认信息",
                    9900200: "默认信息",
                    9900300: "默认信息",
                    9900400: "离校考勤",
                    9900500: "进校考勤"
            };
            console.log(window.innerWidth*0.4)
            document.getElementById("kqvis").style.width = (window.innerWidth*0.4-40)+'px'
            
            let vis = d3.select("#kqvis").append("svg")
                .attr("width", width)
                .attr("height", height);
            let legend = d3.select("#legend").append("svg")
                .attr("width", 160)
                .attr("height", 20 * 2 * 8+10);
            let backLayer = vis.append("g"),
                dotLayer = vis.append("g");
            var tooltip = d3.select("body").append("div")
                .attr("class", "tooltip")
                .style("opacity", 0);
            function preprocessData(data) {
                if(data!=undefined){
                    data.forEach(d=>{
                        let timeStr = d.DataDateTime.split("/");
                        const year = parseInt(timeStr[0]),
                            month = parseInt(timeStr[1]),
                            date = parseInt(timeStr[2].split(' ')[0]),
                            time = timeStr[2].split(' ')[1];
                        d.year = year;
                        d.month = month;
                        d.date = date;
                        d.time = time;
                    });
                    data.sort((a,b) => {
                        if(a.year != b.year) return a.year - b.year;
                        else{
                            if(a.month != b.month) return a.month - b.month;
                            else return a.ControllerID - b.ControllerID;
                        }
                    });
                    let yIdx = 0, currentY = startY, currentM = startM;
                    data.forEach(d=>{
                        d.x = (d.year - startY) * 12 + (d.month - startM);
                        if(currentY != d.year || currentM != d.month) yIdx = 0;
                        d.y = yIdx;
                        yIdx += 1;
                        currentY = d.year;
                        currentM = d.month;
                    });
                }
                
            }
            function drawDotPlot(data){
                const mouseover = d=>{
                        tooltip.transition()
                            .duration(200)
                            .style("opacity", .9);
                        let html = `日期: ${d.DataDateTime} <br/> 信息: ${kqControlTaskMap[d.control_task_order_id]}`;
                        tooltip.html(html)
                            .style("left", (d3.event.pageX) + "px")
                            .style("top", (d3.event.pageY - 100) + "px");
                    },
                    mouseout = d=>{
                        tooltip.transition().duration(500).style("opacity", 0);
                    };
                dotLayer.selectAll(".dot").data(data).enter()
                    .append("circle")
                    .attr("class", "dot")
                    .attr("cx", d=> d.x * dotRadius * 2 + dotRadius)
                    .attr("cy", d=> height - d.y * dotRadius * 2 - dotRadius - textH)
                    .attr("r", dotRadius)
                    .attr("fill", d=>kqColor[kqControllerMap[d.ControllerID].index])
                    .on("mouseover", mouseover)
                    .on("mouseout", mouseout);
            }
            function drawDotBackground(){
                let currentM = startM, currentY = startY;
                let labelTxts = [];
                while(currentY != endY || currentM != endM){
                    labelTxts.push(currentM);
                    currentM += 1;
                    if (currentM > 12) {
                        currentY += 1;
                        currentM = 1;
                    }
                }
                labelTxts.push(endM);
                backLayer.selectAll(".monthtext").data(labelTxts).enter()
                    .append("text")
                    .attr("class", "monthtext")
                    .attr("font-size", "8px").attr("text-anchor", "middle")
                    .attr("x", (d,i)=> dotRadius * 2 * i)
                    .attr("y", height - textH + dotRadius)
                    .attr("dx", dotRadius)
                    .attr("dy", "0.5em")
                    .text(d => d);
                // draw background
                let backgrounds = [];
                currentM = startM;
                currentY = startY;
                while(currentY < endY){
                    backgrounds.push({
                        x1 : (currentY - startY) * 12 + (currentM - startM),
                        x2 : (currentY - startY) * 12 + (currentM - startM) + (12 - currentM),
                        year : currentY
                    });
                    currentM = 1;
                    currentY += 1;
                }
                if(currentY == endY){
                    backgrounds.push({
                        x1: (currentY - startY) * 12 + (currentM - startM),
                        x2 : (currentY - startY) * 12 + (currentM - startM) + (endM - currentM),
                        year : currentY
                    });
                }
                // console.log(backgrounds);
                backLayer.selectAll(".back").data(backgrounds).enter()
                    .append("rect")
                    .attr("class", "back")
                    .attr("x", d=>d.x1 * dotRadius * 2).attr("y", 0)
                    .attr("width", d=>(d.x2 - d.x1 + 1) * dotRadius * 2)
                    .attr("height", height - textH)
                    .attr("fill", d=> (d.year - startY) % 2 == 0 ? "#eee" : "#fff");
                backLayer.selectAll(".year").data(backgrounds).enter()
                    .append("text")
                    .attr("class", "year")
                    .attr("font-size", "8px")
                    .attr("text-anchor", "left")
                    .attr("x", d=> d.x1 * dotRadius * 2)
                    .attr("y", 0)
                    .attr("dy", "1em")
                    .text(d => d.year);
            }
            function drawLegend(){
                // const data = kqControllerMap.values();
                let data = [];
                for(var key in kqControllerMap) data.push(kqControllerMap[key]);
                if(data!=undefined){
                    legend.selectAll(".kq").data(data).enter()
                        .append("text").attr("font-size", "10px").attr("text-anchor", "left")
                        .attr("x", 0)
                        .attr("y", (d, i)=>(i+0.4)*20)
                        .attr("dy", "0.5em")
                        .text(d => d.info);
                    legend.selectAll(".dot").data(data).enter().append("rect")
                        .attr("x", dotRadius*15)
                        .attr("y", (d, i)=>i*20)
                        .attr("width", 19)
                        .attr("height", 19)
                        .attr("r", dotRadius)
                        .attr("fill", d=>kqColor[d.index]);
                    
                }

            }
            function addStudentSelector(json){
                d3.select('#student')
                    .selectAll('option')
                    .data(d3.keys(json))
                    .enter().append('option')
                    .attr("value",d=>d)
                    .text(d=>d)
                    .style("font-size","18px")
                $("#student").change(function(){
                    let value = $(this).find("option:selected").attr("value");
                    // console.log(value);
                    dotLayer.selectAll(".dot").remove();
                    const data = json[value];
                    preprocessData(data);
                    drawDotPlot(data);
                });
            }

                addStudentSelector(this.studentkq);
                drawLegend();
                preprocessData(data);
                console.log(data);
                drawDotBackground();
                drawDotPlot(data);

            }
    }
}
</script>

<style scope="this api replaced by slot-scope in 2.5.0+">
div.tooltip {
    position: absolute;
    text-align: center;
    width: 100px;
    /* height: 200px; */
    padding: 2px;
    font: 12px sans-serif;
    background: lightsteelblue;
    border: 0px;
    border-radius: 8px;
    pointer-events: none;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked + .slider {
    background-color: #2196F3;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

.menu {
    width: 150px;
    height: 50px;
    display: inline-block;
}

#skq{
    display: flex;
    justify-content: start;
    align-items: center;  
     margin-top:-50px; 
}


