her_id":"186","teacher_name":"周老师"},{"teacher_id":"187","teacher_name":"潘老师"},{"teacher_id":"185","teacher_name":"刘老师"}]},{"sub_id":"8.0","teachers":[{"teacher_id":"369","teacher_name":"陈老师"},{"teacher_id":"194","teacher_name":"刘老师"},{"teacher_id":"281","teacher_name":"王老师"}]},{"sub_id":"17.0","teachers":[{"teacher_id":"178","teacher_name":"高老师"},{"teacher_id":"220","teacher_name":"李老师"},{"teacher_id":"223","teacher_name":"陈老师"},{"teacher_id":"181","teacher_name":"王老师"}]},{"sub_id":"5.0","teachers":[{"teacher_id":"170","teacher_name":"俞老师"},{"teacher_id":"259","teacher_name":"周老师"},{"teacher_id":"164","teacher_name":"林老师"}]},{"sub_id":"59.0","teachers":[{"teacher_id":"158","teacher_name":"俞老师"},{"teacher_id":"157","teacher_name":"王老师"},{"teacher_id":"14","teacher_name":"庄老师"}]}];

