<template>
    <div id="dotpanel">
        <p id="rankp" style="font-size:18px;writing-mode: vertical-rl;">考勤排行榜</p>
        <div id="dotplot_rank" class="border_block">
        </div>
        <div id="dotplot" class="border_block">
        </div>
        <p id="rankp" style="font-size:18px;writing-mode: vertical-rl;">考勤信息</p>
    </div>
</template>

<script>
import * as d3 from 'd3'
//import kqdata from './data/studentkq_test.json'
import { Script } from 'vm';
import Vue from 'vue'
import VueRouter from 'vue-router'
import Student from './Student.vue'
import axios from 'axios'

Vue.prototype.axios = axios


Vue.use(VueRouter)


Vue.use(VueRouter)

var routes = [
  { path: '/student', component: Student },
]

var router = new VueRouter({
  routes
})

export default {
    name: "classKq",
    props:['classid'],
    mounted(){
        let thisvue = this;

        axios.get('http://cuteanian.top/eduvis/mock/studentkq_test.json')
        .then(function (response) {
            thisvue.kqdata = response.data;
            thisvue.draw()
        })
    },
    methods:{
        draw(){
            var classkq = this
            var margin = {top: 20, right: 10, bottom: 20, left: 30},
                width = window.innerWidth*0.8 - margin.left - margin.right,
                height = (window.innerWidth / 12. - margin.top - margin.bottom)*0.7;
            // append the svg object to the body of the page
            var svg = d3.select("#stream")
                .append("svg")
                .attr("width", width + margin.left + margin.right)
                .attr("height", height + margin.top + margin.bottom)
                .append("g")
                .attr("transform",
                    "translate(" + margin.left + "," + margin.top + ")");
            let streamLayer = svg.append("g"),
                streamBackLayer = svg.append("g");
            const kqControllerMap = {
                1001: {info: "迟到_晚到", index: 0},
                1002: {info: "校徽_早退", index: 1},
                1003: {info: "操场考勤机", index: 2},
                99001: {info: "迟到[移动考勤机]", index: 3},
                99002: {info: "校服[移动考勤机]", index: 4},
                99003: {info: "早退[移动考勤机]", index: 5},
                99004: {info: "离校[移动考勤机]", index: 6},
                99005: {info: "进校[移动考勤机]", index: 7}
            };
            const startDate = new Date("9/1/2016"),
                endDate = new Date("2/1/2019"),
                daySpan = parseInt((endDate - startDate) / (1000 * 60 * 60 * 24));
            const tickDates = ["9/1/2016", "1/1/2017", "6/1/2017", "9/1/2017", "1/1/2018", "6/1/2018", "9/1/2018", "1/1/2019"];
            const timeFormat = d3.timeFormat("%Y-%m-%d");
            const x = d3.scaleLinear().domain([0, daySpan]).range([ 0, width ]),
                reverseX = d3.scaleTime().domain([0, width]).range([startDate, endDate]),
                xLabel = d3.scaleTime().domain([startDate, endDate]).range([0, width]),
                y = d3.scaleLinear().domain([-5, 5]).range([ height, 0 ]);
            //const kqColor = d3.schemeDark2;
            //const kqColor = ["#ede58d",'#FF8C00',"#fdffe0","#ddf0ed","#FFEF94","#dafacc","#B9AAB9","#ffc0a1"]
            const d3Color = ['#8dd3c7','#ffffb3','#bebada','#fbd462','#80b1d3','#fb8072','#b3de69','#fccde5']

            // const d3Color = []
            // kqColor.forEach((d,i)=>{
            //     console.log(d3.color(d))
            //     d3Color.push(d3.color(d).darker(0.3).toString())
            //     d = d3.color(d).darker(0.3).toString()
            // })

            const dotRadius = 5,
                dotplotW = dotRadius * 2 * (daySpan + 1),
                dotplotH = dotRadius * 2 * 20;
            let dotplotSVG = d3.select("#dotplot")
                .append("svg").attr("id", "dotplotSVG")
                .attr("width", dotplotW + margin.left + margin.right)
                .attr("height", dotplotH + margin.top + margin.bottom)
                .append("g")
                .attr("transform", "translate(" + margin.left + "," + margin.top + ")");
            let dotBackLayer = dotplotSVG.append("g");
            const dotX = d3.scaleLinear().domain([0, daySpan]).range([ 0, dotplotW ]),
                dotReverseX = d3.scaleTime().domain([0, dotplotW]).range([startDate, endDate]),
                dotXLabel = d3.scaleTime().domain([startDate, endDate]).range([0, dotplotW]);
            // check if user select a dot
            let selectId = "";
            let tooltip = d3.select("body").append("div")
                .attr("class", "tooltip")
                .style("height",200)
                .style("opacity", 0);
            function getEmptyItem(day){
                let item = {day: day};
                d3.keys(kqControllerMap).forEach(key=>{
                    item[key] = 0;
                });
                return item;
            }
            function getDotPlotData(data) {
                let result = [];
                data.forEach(student=>{
                    student.kqdata.forEach(d=>{
                        d.stud_id = student.bf_studentID;
                        d.stud_name = student.bf_Name;
                        d.cla_id = student.bf_classid;
                        d.cla_name = student.cla_Name;
                        result.push(d);
                    });
                });
                return result;
            }
            function drawDotPlot(data){
                // console.log(data);
                // draw background
                let back = dotBackLayer.append("rect")
                    .attr("id", "dotplotBack")
                    .attr("x", 0)
                    .attr("y", 0)
                    .attr("width", dotplotW)
                    .attr("height", dotplotH + margin.top + margin.bottom)
                    .attr("fill", "white");
                dotplotSVG.append('g')
                    .call(d3.axisTop(dotXLabel)
                        .tickFormat(timeFormat)
                        .tickValues(tickDates.map(function(d) { return new Date(d)}) ));
                let result = data.filter(d=>d.day >= 0)
                    .sort((a,b)=>{
                        if(a.day == b.day){
                            if(a.ControllerID == b.ControllerID){
                                return parseInt(a.cla_id) - parseInt(b.cla_id);
                            }
                            return a.ControllerID - b.ControllerID;
                        }
                        return a.day - b.day;
                    });
                let currentX = 0, currentY = 0;
                result.forEach(d=>{
                    if(d.day > currentX) {
                        currentY = 0;
                        currentX = d.day;
                    }
                    d.y = currentY;
                    currentY += 1;
                });
                const line = d3.line().x(d=>d[0]).y(d=>d[1]);
                let highlight = dotplotSVG.append("path")
                    .attr("d", line([[0, 0], [0, dotplotH]]))
                    .attr("stroke", "black")
                    .attr("stroke-width", 1)
                    .attr("opacity", 0);
                let dateText = dotplotSVG.append("text")
                    .attr("font-size", "12px")
                    .attr("text-anchor", "left")
                    .attr("x", "0.5em")
                    .attr("y", 0)
                    .attr("dx", 0)
                    .attr("dy", "0.5em");
                const mouseover = d=>{
                        console.log(d)
                        dotplotSVG.selectAll(`.dot`).attr("opacity", selectId == "" ? 0.6 : 0.1);
                        if(selectId != "") dotplotSVG.selectAll(`.dot-${selectId}`).attr("opacity", 1);
                        dotplotSVG.selectAll(`.dot-${d.stud_id}`).attr("opacity", 1);
                        tooltip.transition()
                            .duration(200)
                            .style("opacity", .9)
                            .attr("height",200)
                        let type = kqControllerMap[d.ControllerID].info

                        let html = `姓名: ${d.stud_name}<br/>班级: ${d.cla_name}<br/>时间: ${d.DataDateTime}<br/>类型: ${type}`;
                        tooltip.html(html)
                            .style("left", (d3.event.pageX) + "px")
                            .style("top", (d3.event.pageY - 28) + "px");
                        // d3.selectAll(".dot-" + d.mes_StudentID.toString()).attr("fill", "yellow");
                    },
                    mouseout = ()=>{
                        dotplotSVG.selectAll('.dot').attr("opacity", selectId == "" ? 0.6 : 0.1);
                        dotplotSVG.selectAll(`.dot-${selectId}`).attr("opacity", 1);
                        tooltip.transition().duration(500).style("opacity", 0);
                        // d3.selectAll(".dot-" + d.mes_StudentID.toString())
                        //     .attr("fill", d=>d.bf_sex == '男' ? sexColors[0] : sexColors[1]);
                    },
                    dblclick = d=>{
                        router.push({
                            path: '/student',
                            query: {
                                studentId: d.stud_id
                            }
                        });
                        router.go()
                    },
                    click = d=>{
                        selectId = d.stud_id;
                        dotplotSVG.selectAll('.dot').attr("opacity", 0.1);
                        dotplotSVG.selectAll(`.dot-${d.stud_id}`).attr("opacity", 1);
                        d3.selectAll(".bar").attr("opacity", 0.5);
                        d3.selectAll(`.bar-${d.stud_id}`).attr("opacity", 1);
                        classkq.$emit("chooseStudent",selectId)
                    },
                    unclick = ()=>{
                        selectId = "";
                        dotplotSVG.selectAll(".dot").attr("opacity", 0.6);
                        d3.selectAll(".bar").attr("opacity", 1);
                    }
                dotplotSVG.selectAll(".dott").data(result).enter().append("circle")
                    .attr("class", d=>`dott dott-${d.stud_id}`)
                    .attr("cx", d=>d.day * dotRadius * 2 + dotRadius)
                    .attr("cy", d=>d.y * dotRadius * 2 + dotRadius)
                    .attr("r", dotRadius)
                    .attr("fill", d=>d3Color[kqControllerMap[d.ControllerID].index])
                    .attr("opacity", 0.6)
                    .on("mouseover", mouseover)
                    .on("mouseout", mouseout)
                    .on("click", click)
                    .on("dblclick",dblclick)
                const backmousemove = ()=>{
                        let mouse = d3.mouse(d3.select('#dotplotBack').node());
                        highlight.attr("opacity", 1)
                            .attr("d", line([[mouse[0], 0], [mouse[0], dotplotH]]));
                        dateText.attr("opacity", 1).attr("x", mouse[0]).text(timeFormat(dotReverseX(mouse[0])));
                    },
                    backmouseout = ()=>{
                        highlight.transition().duration(500).attr("opacity", 0);
                        dateText.transition().duration(500).attr("opacity", 0);
                    };
                back.on("click", unclick)
                    .on("mousemove", backmousemove)
                    .on("mouseout", backmouseout);
                dotBackLayer
                    .call( d3.brushX()                     // Add the brush feature using the d3.brush function
                        .extent( [ [0,0], [dotplotW, dotplotH] ] )
                        .on("end", updateChart)
                    );
                function updateChart() {
                    // Get the selection coordinate
                    let extent = d3.event.selection;
                    d3.select("#dotplot_rank").select("svg").remove();
                    if(extent){
                        let dayExtend = [parseInt((dotReverseX(extent[0]) - startDate) / (1000 * 60 * 60 * 24)),
                            parseInt((dotReverseX(extent[1]) - startDate) / (1000 * 60 * 60 * 24))];
                        drawTopList(result.filter(d=> d.day >= dayExtend[0] && d.day <= dayExtend[1]));
                    }
                    else{
                        drawTopList(result);
                    }
                }
            }
            function drawTopList(data){
                const widthBar = 20, marginBar = 2;
                let result = d3.nest()
                    .key(d=>d.stud_id)
                    .rollup(function(leaves) { return leaves; })
                    .entries(data)
                    .sort((a,b)=>b.value.length-a.value.length);
                const heightToplist = (widthBar + marginBar) * result.length;
                let toplistSVG = d3.select("#dotplot_rank")
                    .append("svg").attr("width", 100).attr("height", heightToplist)
                    .append("g");
                let back = toplistSVG.append("rect").attr("x", 0).attr("y", 0)
                    .attr("width", 100).attr("height", heightToplist).attr("fill", "white");
                let studentMap = d3.map();
                for(let i = 0; i < result.length; i++){
                    let item = {
                        stud_id: result[i].key,
                        count: result[i].value.length,
                        idx: i,
                        stud_name: result[i].value[0].stud_name,
                        cla_id: result[i].value[0].cla_id,
                        cla_name: result[i].value[0].cla_name
                    };
                    d3.keys(kqControllerMap).forEach(key=>{
                        item[key] = 0;
                    });
                    studentMap.set(result[i].key, item);
                }
                // console.log(studentMap);
                data.forEach(d=>{
                    studentMap.get(d.stud_id)[d.ControllerID] += 1;
                });
                const topStudData = studentMap.values().sort((a,b) => b.count - a.count);
                let stack = d3.stack()
                    .keys(d3.keys(kqControllerMap))
                    .order(d3.stackOrderNone)
                    .offset(d3.stackOffsetNone);
                let series = stack(topStudData);
                // console.log(series);
                // set the scale of x and y
                let ydomain = [], yrange = [];
                studentMap.keys().forEach((key)=>{
                    ydomain.push(key);
                    yrange.push(studentMap.get(key).idx * (widthBar + marginBar));
                });
                const y = d3.scaleOrdinal()
                    .domain(ydomain)
                    .range(yrange);
                // .rangeRoundBands([10, 300], 0.02);
                const x = d3.scaleLinear()
                    .domain([0, d3.max(topStudData, d=>d.count)])
                    .range([0, 100]);
                let groups = toplistSVG
                    .selectAll(".bargroup")
                    .data(series)
                    .enter().append("g")
                    .attr("class", "bargroup")
                    .attr("fill", d=>d3Color[kqControllerMap[d.key].index]);
                const mouseover = d=>{
                        tooltip.transition()
                            .duration(200)
                            .style("opacity", .9);
                        let html = `姓名: ${d.data.stud_name}<br/>班级: ${d.data.cla_name}<br/>总数: ${d.data.count}`;
                        tooltip.html(html)
                            .style("left", (d3.event.pageX) + "px")
                            .style("top", (d3.event.pageY - 28) + "px");
                    },
                    mouseout = d=>{
                        tooltip.transition().duration(500).style("opacity", 0);
                    },
                    click = d=>{
                        selectId = d.data.stud_id;
                        dotplotSVG.selectAll('.dot').attr("opacity", 0.1);
                        dotplotSVG.selectAll(`.dot-${selectId}`).attr("opacity", 1);
                        toplistSVG.selectAll(".bar").attr("opacity", 0.5);
                        toplistSVG.selectAll(`.bar-${d.data.stud_id}`).attr("opacity", 1);
                        classkq.$emit("chooseStudent",selectId)

                        // d3.select(this.parentNode).attr("opacity", 1);
                    },
                    unclick = ()=>{
                        selectId = "";
                        dotplotSVG.selectAll(".dot").attr("opacity", 0.6);
                        toplistSVG.selectAll(".bar").attr("opacity", 1);
                    },
                    dblclick = d=>{
                        console.log(d)
                        router.push({
                            path: '/student',
                            query: {
                                studentId: d.data.stud_id
                            }
                        });
                        router.go()
                    };
                groups.selectAll("rect")
                    .data(d => d).enter()
                    .append("rect")
                    .attr("class", d=> `bar bar-${d.data.stud_id}`)
                    .attr("x", d=> x(d[0]))
                    .attr("y", d=> y(d.data.stud_id))
                    .attr("height", widthBar)
                    .attr("width", d=>x(d[1]) - x(d[0]))
                    .on("mouseover", mouseover)
                    .on("mouseout", mouseout)
                    .on("click", click)
                    .on("dblclick",dblclick)
                back.on("click", unclick);
            }

                function initData(){
                    let data = [];
                    for(let i = 0; i < daySpan; i++){
                        data.push(getEmptyItem(i));
                    }
                    return data;
                }

            let data = initData();
            this.kqdata.forEach(student=>{
                student.kqdata.forEach(d=>{
                    let time = d.DataDateTime.split(' ')[0].split('/');
                    let date = new Date(time[0], time[1] - 1, time[2]);
                    let diffDays = parseInt((date - startDate) / (1000 * 60 * 60 * 24));
                    d.day = diffDays;
                    if(diffDays > 0) data[diffDays][d.ControllerID] += 1;
                });
            });
            const result = getDotPlotData(this.kqdata);
            let filteredData = result.filter(d=>d.cla_id == this.classid);
            drawDotPlot(filteredData);
            drawTopList(filteredData, 10); // show the top 10 students
        }
    }
}

</Script>

<style>
     #dotpanel{
         display:flex;
         justify-content: center

     }


     .border_block{
            border:2px solid #000;
            overflow-y: auto;
            position: relative;
        }
        div#dotplot_rank{
            width: 120px;
            height: calc(100vh * 0.16);
        }
        div#dotplot{
            width: 80%;
            /* margin-right:3%;*/
            height: calc(100vh * 0.16);
        }
</style>
