<template>
    <div>
        <div class="radarChart"></div>
        <div class="buttons">
            <button class="button" style="background-color:#8dd3c7" v-on:click="changeall">总体</button>
            <button class="button" style="background-color:#ffffb3" v-on:click="changefinal">期末</button>
            <button class="button" style="background-color:#bebada" v-on:click="changemiddle">期中</button>
            <button class="button" style="background-color:white" v-on:click="change">还原</button>
        </div>
    </div>

    
</template>

<script>
import * as d3 from 'd3'
//import studentdata from './data/studentGrade_exam.json'
//import studentdata from './data/studentScore_57.json'
//import maxavi from "./data/examavemax.json"
import Vue from 'vue'
import Student from './Student.vue'
import axios from 'axios'

Vue.prototype.axios = axios



export default {
    name: "studentGradeSum",
    props:['studentId','choose'],
    data(){
        return{
            studentGradeSum:"",
            totaldata:[],
            finaldata:[],
            middledata:[],
            data:[]
        }
    },
    mounted(){
        let thisvue = this;

        axios.get('http://cuteanian.top/eduvis/mock/studentScore_57.json')
        .then(function (response) {
            thisvue.studentdata = response.data;
            let thisStudent = thisvue.studentdata[thisvue.studentId]
            let result = []
            let middleresult = []
            let finalresult = []
            let examnumber = [279,288,298,267]
            let middlenumber = [284,304,265,271]
            thisStudent.forEach(element => {
                if(element['mes_sub_name']!='总分'){
                    let subname = element['mes_sub_name']
                    let scores = element['scores']
                    let sum=[]
                    let middlesum = []
                    let finalsum = []
                    let thissub = {}
                    let finalsub = {}
                    let middlesub = {}
                    scores.forEach(element =>{
                        let thisProb = element['Scoringrate']
                        sum.push(thisProb) 
                        let number = element['exam_number']
                        if(examnumber.indexOf(number)>=0){
                            finalsum.push(thisProb)
                        }
                        else if(middlenumber.indexOf(number)){
                            middlesum.push(thisProb)
                        }

                    })
                    const average = arr => arr.reduce((acc, val) => acc + val, 0) / arr.length;
                    
                    thissub['axis'] = subname
                    finalsub['axis'] = subname
                    middlesub['axis'] = subname
                    thissub['value'] = average(sum)
                    finalsub['value'] = average(finalsum)
                    middlesub['value'] = average(middlesum)
                    console.log(thissub)

                    result.push(thissub)
                    finalresult.push(finalsub)
                    middleresult.push(middlesub)

                }
            });
            let data = [{
                name:0,
                axes:result
            },{
                name:1,
                axes:finalresult
            },{
                name:2,
                axes:middleresult
            }]
            thisvue.data = data;
            thisvue.finaldata = [data[1]]
            thisvue.middledata = [data[2]]
            thisvue.totaldata = [data[0]]
            thisvue.drawradar(data,thisvue.chooses)
        })
        
    },
    computed:{
        data : function(){
            
        },
        chooses: function(){
            return this.choose+" "
        }
    },
    methods:{
        changeall(){
            d3.selectAll(".radarWrapper").remove()
            let flag=0;
            let data = []
            this.data.forEach(function(d,i){
                if(d.name==0){
                    flag=1
                }else{
                    data.push(d)
                }
            })
            console.log(flag)
            if(flag==0){
                data.push(this.totaldata[0])
            }
            this.data = data
            console.log(this.data)
             this.drawradar(data,this.chooses)

        },
        changefinal(){
            d3.selectAll(".radarWrapper").remove()
            let flag=0;
            let data = []
            this.data.forEach(function(d,i){
                if(d.name==1){
                    flag=1
                }else{
                    data.push(d)
                }
            })
            console.log(flag)
            if(flag==0){
                data.push(this.finaldata[0])
            }
            this.data = data
            console.log(this.data)
            this.drawradar(data,this.chooses)

        },
        changemiddle(){
            d3.selectAll(".radarWrapper").remove()
            let flag=0;
            let data = []
            this.data.forEach(function(d,i){
                if(d.name==2){
                    flag=1
                }else{
                    data.push(d)
                }
            })
            console.log(flag)
            if(flag==0){
                data.push(this.middledata[0])
            }
            this.data = data
            console.log(this.data)
             this.drawradar(data,this.chooses)

        },
        change(){
            d3.selectAll(".radarWrapper").remove()
            let data = [this.totaldata[0],this.finaldata[0],this.middledata[0]]
            this.data = data;
             this.drawradar(data,this.chooses)

        },
        drawradar(data,chooses){
            var margin = { top: 50, right: 80, bottom: 50, left: 80 };
				//width = Math.min(400, window.innerWidth / 8) - margin.left - margin.right,
				//height = Math.min(width, window.innerHeight - margin.top - margin.bottom);
            var width = window.innerWidth*0.3;
            var height = window.innerWidth*0.1;
			// // var data = [
			// // 	{ name: 'Allocated budget',
			// // 		axes: [
			// // 			{axis: 'Sales', value: 42},
			// // 			{axis: 'Marketing', value: 20},
			// // 			{axis: 'Development', value: 60},
			// // 			{axis: 'Customer Support', value: 26},
			// // 			{axis: 'Information Technology', value: 35},
			// // 			{axis: 'Administration', value: 20}
			// // 		]
			// // 	}
			// // ];
			var radarChartOptions = {
			  w: window.innerWidth*0.1,
			  h: window.innerWidth*0.1,
			  margin: margin,
			  levels: 5,
			  roundStrokes: true,
				//color: d3.scaleOrdinal().range(["#26AF32", "#762712"]),
				format: '.0f'
			};
			// Draw the chart, get a reference the created svg element :
            let svg_radar1 = this.RadarChart(".radarChart", data, radarChartOptions,chooses,'value');


            
				

        },
        RadarChart(parent_selector, data, options,chooses,type){
            let chooseArray = []
            let thisData = this.studentdata[this.studentId]
            thisData.forEach(function(d,i){
                var subname = d['mes_sub_name']
                d.scores.forEach(function(exam,i){
                    if(exam['exam_number']==305 & subname!="语文" &subname!="数学" & subname!="英语" & subname!="总分")
                        chooseArray.push(subname)            
                })
            })

            // this.chooseArray = choose[0]+","+choose[1]+","+choose[2]
            //console.log(choose)


            const max = Math.max;
            const sin = Math.sin;
            const cos = Math.cos;
            const HALF_PI = Math.PI / 2;
            //Wraps SVG text - Taken from http://bl.ocks.org/mbostock/7555321
            const wrap = (text, width) => {
            text.each(function() {
                    var text = d3.select(this),
                        words = text.text().split(/\s+/).reverse(),
                        word,
                        line = [],
                        lineNumber = 0,
                        lineHeight = 1.4, // ems
                        y = text.attr("y"),
                        x = text.attr("x"),
                        dy = parseFloat(text.attr("dy")),
                        tspan = text.text(null).append("tspan").attr("x", x).attr("y", y).attr("dy", dy + "em");

                    while (word = words.pop()) {
                    line.push(word);
                    tspan.text(line.join(" "));
                    if (tspan.node().getComputedTextLength() > width) {
                            line.pop();
                            tspan.text(line.join(" "));
                            line = [word];
                            tspan = text.append("tspan").attr("x", x).attr("y", y).attr("dy", ++lineNumber * lineHeight + dy + "em").text(word);
                    }
                    }
            });
            }//wrap

            const cfg = {
            w: window.innerWidth*0.3,				//Width of the circle
            h: window.innerWidth*0.3,				//Height of the circle
            margin: {top: 20, right: 20, bottom: 20, left: 20}, //The margins of the SVG
            levels: 3,				//How many levels or inner circles should there be drawn
            maxValue: 0, 			//What is the value that the biggest circle will represent
            labelFactor: 1.25, 	//How much farther than the radius of the outer circle should the labels be placed
            wrapWidth: 60, 		//The number of pixels after which a label needs to be given a new line
            opacityArea: 0.35, 	//The opacity of the area of the blob
            dotRadius: 8, 			//The size of the colored circles of each blog
            opacityCircles: 0.1, 	//The opacity of the circles of each blob
            strokeWidth: 2, 		//The width of the stroke around each blob
            color:['#8dd3c7','#ffffb3','#bebada'],
            roundStrokes: false,	//If true the area and stroke will follow a round path (cardinal-closed)
            //color: d3.scaleOrdinal(d3.schemeCategory10),	//Color function,
            format: '.2%',
            unit: '',
            legend: false
            };

            //Put all of the options into a variable called cfg
            if('undefined' !== typeof options){
            for(var i in options){
                if('undefined' !== typeof options[i]){ cfg[i] = options[i]; }
            }//for i
            }//if

            //If the supplied maxValue is smaller than the actual one, replace by the max in the data
            // var maxValue = max(cfg.maxValue, d3.max(data, function(i){return d3.max(i.map(function(o){return o.value;}))}));
            let maxValue = 0;
            for (let j=0; j < data.length; j++) {
                for (let i = 0; i < data[j].axes.length; i++) {
                    data[j].axes[i]['id'] = data[j].name;
                    if (data[j].axes[i]['value'] > maxValue) {
                        maxValue = data[j].axes[i]['value'];
                    }
                }
            }
            //maxValue = max(cfg.maxValue, maxValue);

            maxValue = 100
            const allAxis = data[0].axes.map((i, j) => i.axis),	//Names of each axis
                total = allAxis.length,					//The number of different axes
                radius = Math.min(cfg.w/2, cfg.h/2), 	//Radius of the outermost circle
                Format = d3.format(cfg.format),			 	//Formatting
                angleSlice = Math.PI * 2 / total;		//The width in radians of each "slice"

            //Scale for the radius
            const rScale = d3.scaleLinear()
                .range([0, radius])
                .domain([0, maxValue]);

            /////////////////////////////////////////////////////////
            //////////// Create the container SVG and g /////////////
            /////////////////////////////////////////////////////////
            const parent = d3.select(parent_selector);

            //Remove whatever chart with the same id/class was present before
            parent.select("svg").remove();

            //Initiate the radar chart SVG
            let svg = parent.append("svg")
                    .attr("width",  cfg.w + cfg.margin.left + cfg.margin.right)
                    .attr("height", cfg.h + cfg.margin.top + cfg.margin.bottom)
                    .attr("class", "radar");

            //Append a g element
            let g = svg.append("g")
                    .attr("transform", "translate(" + (cfg.w/2 + cfg.margin.left) + "," + (cfg.h/2 + cfg.margin.top) + ")");

            /////////////////////////////////////////////////////////
            ////////// Glow filter for some extra pizzazz ///////////
            /////////////////////////////////////////////////////////

            //Filter for the outside glow
            let filter = g.append('defs').append('filter').attr('id','glow'),
                feGaussianBlur = filter.append('feGaussianBlur').attr('stdDeviation','2.5').attr('result','coloredBlur'),
                feMerge = filter.append('feMerge'),
                feMergeNode_1 = feMerge.append('feMergeNode').attr('in','coloredBlur'),
                feMergeNode_2 = feMerge.append('feMergeNode').attr('in','SourceGraphic');

            /////////////////////////////////////////////////////////
            /////////////// Draw the Circular grid //////////////////
            /////////////////////////////////////////////////////////

            //Wrapper for the grid & axes
            let axisGrid = g.append("g").attr("class", "axisWrapper");

            //Draw the background circles
            axisGrid.selectAll(".levels")
            .data(d3.range(1,(cfg.levels+1)).reverse())
            .enter()
                .append("circle")
                .attr("class", "gridCircle")
                .attr("r", d => radius / cfg.levels * d)
                .style("fill", "#CDCDCD")
                .style("stroke", function(d,i){
                    if(d==3){
                        return "red"
                    }
                    else{
                        return "#CDCDCD"
                    }
                })
                .style("fill-opacity", cfg.opacityCircles)
                .style("filter" , "url(#glow)");

            //Text indicating at what % each level is
            axisGrid.selectAll(".axisLabel")
            .data(d3.range(1,(cfg.levels+1)).reverse())
            .enter().append("text")
            .attr("class", "axisLabel")
            .attr("x", 4)
            .attr("y", d => -d * radius / cfg.levels)
            .attr("dy", "0.4em")
            .style("font-size", "10px")
            .attr("fill", "#737373")
            .text(d => Format(maxValue * d / cfg.levels) + cfg.unit);

            /////////////////////////////////////////////////////////
            //////////////////// Draw the axes //////////////////////
            /////////////////////////////////////////////////////////

            //Create the straight lines radiating outward from the center
            var axis = axisGrid.selectAll(".axis")
                .data(allAxis)
                .enter()
                .append("g")
                .attr("class", "axis");
            //Append the lines
            axis.append("line")
                .attr("x1", 0)
                .attr("y1", 0)
                .attr("x2", (d, i) => rScale(maxValue *1.1) * cos(angleSlice * i - HALF_PI))
                .attr("y2", (d, i) => rScale(maxValue* 1.1) * sin(angleSlice * i - HALF_PI))
                .attr("class", "line")
                .style("stroke", "white")
                .style("stroke-width", "2px");

            //Append the labels at each axis
            axis.append("text")
                .attr("class", "legend")
                .style("font-size", "15px")
                .style("stroke",function(d){
                    if(chooseArray.indexOf(d)>=0 | d=="数学" | d=='语文' |d =='英语'){
                        return "red"
                    }
                })
                .attr("text-anchor", "middle")
                .attr("dy", "0.35em")
                .attr("x", (d,i) => rScale(maxValue * cfg.labelFactor) * cos(angleSlice * i - HALF_PI))
                .attr("y", (d,i) => rScale(maxValue * cfg.labelFactor) * sin(angleSlice * i - HALF_PI))
                .text(d => d)
                .call(wrap, cfg.wrapWidth);

            /////////////////////////////////////////////////////////
            ///////////// Draw the radar chart blobs ////////////////
            /////////////////////////////////////////////////////////

            //The radial line function
            const radarLine = d3.radialLine()
                .curve(d3.curveLinearClosed)
                .radius(d => rScale(d[type]))
                .angle((d,i) => i * angleSlice);

            if(cfg.roundStrokes) {
                radarLine.curve(d3.curveCardinalClosed)
            }

            //Create a wrapper for the blobs
            const blobWrapper = g.selectAll(".radarWrapper")
                .data(data)
                .enter().append("g")
                .attr("class", "radarWrapper");

            //Append the backgrounds
            blobWrapper
                .append("path")
                .attr("class", "radarArea")
                .attr("d", d => radarLine(d.axes))
                 .style("stroke", (d,i) => cfg.color[d.id])
                //.style("fill", "black")
                .style("fill", (d,i) => {
                    cfg.color[d.id]})
                .style("fill-opacity", cfg.opacityArea)
                .on('mouseover', function(d, i) {
                    //Dim all blobs
                    parent.selectAll(".radarArea")
                        .transition().duration(200)
                        .style("fill-opacity", 0.1);
                    //Bring back the hovered over blob
                    d3.select(this)
                        .transition().duration(200)
                        .style("fill-opacity", 0.7);
                })
                .on('mouseout', () => {
                    //Bring back all blobs
                    parent.selectAll(".radarArea")
                        .transition().duration(200)
                        .style("fill-opacity", cfg.opacityArea);
                });

            //Create the outlines
            blobWrapper.append("path")
                .attr("class", "radarStroke")
                .attr("d", function(d,i) { return radarLine(d.axes); })
                .style("stroke-width", cfg.strokeWidth + "px")
                //.style("fill", "black")
                .style("stroke", (d,i) => {
                    
                    return cfg.color[d.name]})
                .style("fill", "none")
                .style("filter" , "url(#glow)");

            //Append the circles
            blobWrapper.selectAll(".radarCircle")
                .data(d => d.axes)
                .enter()
                .append("circle")
                .attr("class", "radarCircle")
                .attr("r", cfg.dotRadius)
                .attr("cx", (d,i) => rScale(d.value) * cos(angleSlice * i - HALF_PI))
                .attr("cy", (d,i) => rScale(d.value) * sin(angleSlice * i - HALF_PI))
                .style("fill", (d,i) => {
                    //console.log(d.id)
                    return cfg.color[d.id]})
                //.style("fill", "black")
                .style("fill-opacity", 0.8);

            /////////////////////////////////////////////////////////
            //////// Append invisible circles for tooltip ///////////
            /////////////////////////////////////////////////////////

            //Wrapper for the invisible circles on top
            const blobCircleWrapper = g.selectAll(".radarCircleWrapper")
                .data(data)
                .enter().append("g")
                .attr("class", "radarCircleWrapper");

            //Append a set of invisible circles on top for the mouseover pop-up
            blobCircleWrapper.selectAll(".radarInvisibleCircle")
                .data(d => d.axes)
                .enter().append("circle")
                .attr("class", "radarInvisibleCircle")
                .attr("r", cfg.dotRadius * 1.5)
                .attr("cx", (d,i) => rScale(d.value) * cos(angleSlice*i - HALF_PI))
                .attr("cy", (d,i) => rScale(d.value) * sin(angleSlice*i - HALF_PI))
                .style("fill", "none")
                .style("pointer-events", "all")
                .on("mouseover", function(d,i) {
                    tooltip
                        .attr('x', this.cx.baseVal.value - 10)
                        .attr('y', this.cy.baseVal.value - 10)
                        .transition()
                        .style('display', 'block')
                        .text(Format(d.value) + cfg.unit);
                })
                .on("mouseout", function(){
                    tooltip.transition()
                        .style('display', 'none').text('');
                });

            const tooltip = g.append("text")
                .attr("class", "tooltip")
                .attr('x', 0)
                .attr('y', 0)
                .style("font-size", "20px")
                .style('display', 'none')
                .attr("text-anchor", "middle")
                .attr("dy", "0.35em");

            if (cfg.legend !== false && typeof cfg.legend === "object") {
                let legendZone = svg.append('g');
                let names = data.map(el => el.name);
                if (cfg.legend.title) {
                    let title = legendZone.append("text")
                        .attr("class", "title")
                        .attr('transform', `translate(${cfg.legend.translateX},${cfg.legend.translateY})`)
                        .attr("x", cfg.w - 70)
                        .attr("y", 10)
                        .attr("font-size", "12px")
                        .attr("fill", "#404040")
                        .text(cfg.legend.title);
                }
                let legend = legendZone.append("g")
                    .attr("class", "legend")
                    .attr("height", 100)
                    .attr("width", 200)
                    .attr('transform', `translate(${cfg.legend.translateX},${cfg.legend.translateY + 20})`);
                // Create rectangles markers
                legend.selectAll('rect')
                .data(names)
                .enter()
                .append("rect")
                .attr("x", cfg.w - 65)
                .attr("y", (d,i) => i * 20)
                .attr("width", 10)
                .attr("height", 10)
                //.style("fill", "black")
                .style("fill", (d,i) => {
                    console.log(i)
                    cfg.color[i]});
                // Create labels
                legend.selectAll('text')
                .data(names)
                .enter()
                .append("text")
                .attr("x", cfg.w - 52)
                .attr("y", (d,i) => i * 20 + 9)
                .attr("font-size", "11px")
                .attr("fill", "#737373")
                .text(d => d);
            }
            return svg;              
        }
    }
}

</script>

<style scope="this api replaced by slot-scope in 2.5.0+">
    body {
        font-family: 'Open Sans', sans-serif;
        font-size: 11px;
        font-weight: 300;
        fill: #242424;
        text-align: center;
        text-shadow: 0 1px 0 #fff, 1px 0 0 #fff, -1px 0 0 #fff, 0 -1px 0 #fff;
        cursor: default;
    }

    .legend {
        font-family: 'Raleway', sans-serif;
        fill: #333333;
    }

    .radarChart{
        display: flex;
        justify-content: center;
    }
    .button{
        width:40px;
        height:30px;
        margin-left: 10px;
    }
</style>
